import Vue from 'vue'
import Router from 'vue-router'
import abilitymap from '@/components/am'
import simplemarkedown from '@/components/sm'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/abilitymap',
      component: abilitymap
    },
    {
      path: '/simplemarkedown',
      component: simplemarkedown
    }
  ]
})



